import MescrollVue from 'mescroll.js/mescroll.vue'
import "mescroll.js/mescroll.min.css";
import { Indicator } from "mint-ui";
import { selectCart } from "../../server/employee";
import { log } from 'util';
export default {
  name: "client",
  components:{MescrollVue },
  data() {
    return {
      imgUrl: require("@/assets/img/seach.png"),
      value: "",
      count: 0,
      mescroll:null,
       mescrollDown:{ // 下拉加载的配置.
        callback:this.downCallBack,
        clearEmptyId:"main",
						isBoth: false, 
            isBounce: true,
             
         },
      mescrollUp: { // 上拉加载的配置.
          click: true,
          callback: this.getList,
          htmlNodata: '<p class="upwarp-nodata">  </p>',
          noMoreSize: 5, 
          page: {
            num: 0,
            size: 50,
            total: 0
          },
          empty: {
          warpId: "main", 
          // icon: "./static/mescroll/mescroll-empty.png", 
          tip: "暂无相关数据~" //提示
          },
        htmlLoading:'<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">加载中..</p>',
      },
      pages: {
        page: 1,
        size: 50,
        total: 0
      },
      dataList: [] // 列表数据
    };
  },
  watch: {
    value: function (newval,oldval) {
      if (this.value == "") {
        this.placeholder='请输入关键字'
        this.handleSeach()
      }else{
        this.placeholder=''
      }
    }
  },
  methods: {
    handleCommentFocus(){
      this.placeholder=''
    },
    mescrollInit (mescroll) {
      this.mescroll = mescroll  
    },
    downCallBack(mescroll){
          setTimeout(function(){
					mescroll.endSuccess()
				},1500);
  },
    getList(page, mescroll) {
    const that = this
      selectCart({
        page: page.num,
        limit: page.size,
        companyName:this.value
      }).then(e => {
        if (e.data.code == 200) {
          var arr = e.data.data
           let data = page.num == 1 ? [] : this.dataList;
           this.dataList = this.dataList.concat(arr)
           this.$nextTick(() => {
              this.mescroll.endSuccess(arr.length)
            })
        }
      });
    },
   // 搜索列表数据
    search(){
      this.handleSeach()
    },
     handleSeach(){
       let searchList = []
       const that = this
      selectCart({
        page: this.pages.page,
        limit: this.pages.size,
        companyName:this.value
      }).then(e => {
        if (e.data.code == 200) {
          this.searchList=e.data.data
          this.dataList=this.searchList
        }
      });
    },
    handleLink(companyNo,companyName){
      this.$router.push({
        path:'/AddEmployee',
      })
      localStorage.setItem("companyNo", JSON.stringify(companyNo))
      localStorage.setItem("companyName", JSON.stringify(companyName))
    }
  }
};



